body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#img01 {
  margin: auto;
  display: block;
  width: 80%;
  transition: transform 0.3s ease-in-out;
}

.zoom-in {
  transform: scale(0);
  animation: zoomIn 0.3s forwards;
}

@keyframes zoomIn {
  from {
    transform: scale(0);
  }
  to {
    transform: scale(1);
  }
}

.close {
  position: absolute;
  top: 0;
  right: 100px;
  color: #ffffff;
  font-size: 40px;
  font-weight: bold;
  transition: 0.3s;
  z-index: 9999;
}

.close:hover,
.close:focus {
  color: #bbb;
  text-decoration: none;
  cursor: pointer;
}

.symbol.symbol-md-100px.symbol-2by3>img {
  height: 100px;
  width: 123px;
  max-width: auto;
}

@media only screen and (min-width: 2000) and (max-width:2550){
  .close {
    right:30%;
  }
}

@media only screen and (max-width: 756px){
  .modal-content {
    width: 100%;
  }
  .close {
    color:#000;
  }
}

#screenshot {
  border-radius: 5px;
  cursor: pointer;
  transition: 0.3s;
}

#screenshot:hover {opacity: 0.7;}