.loader {
    width: 120px;
    height: 20px;
    border-radius: 20px;
    background:
        repeating-linear-gradient(135deg,#1a99ce 0 10px,rgb(237, 246, 249) 0 20px) 0/0%   no-repeat,
        repeating-linear-gradient(135deg,#ddd    0 10px,#eee    0 20px) 0/100%;
    animation: l3 2s infinite;
}
@keyframes l3 {
    100% {background-size:100%}
}